.main-btn {
  position: relative;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-decoration: none;
  padding: 8px 16px;
  overflow: hidden;
  border-radius: 7px;
  border: 2px solid var(--circle-theme);
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: transparent;
}
.main-btn::before, .main-btn::after {
  content: "";
  width: 100%;
  height: 25%;
  display: block;
  position: absolute;
  top: initial;
  right: initial;
  bottom: initial;
  left: initial;
  z-index: initial;
  background-color: var(--circle-theme);
  -webkit-transition: all linear 0.5s 0.5s, height linear 0.5s;
  transition: all linear 0.5s 0.5s, height linear 0.5s;
}
.main-btn::before {
  top: 0%;
  right: -100%;
}
.main-btn::after {
  bottom: 0%;
  left: -100%;
}
.main-btn span {
  position: relative;
  z-index: 100;
  color: var(--circle-theme);
  font-weight: 700;
  font-size: 18px;
  -webkit-transition: color ease-out 0.5s 0.1s;
  transition: color ease-out 0.5s 0.1s;
}
@media (max-width: 1300px) {
  .main-btn span {
    font-size: 13px;
  }
}
@media (min-width: 371px) and (max-width: 1300px) {
  .main-btn span {
    font-size: 14px;
  }
}
.main-btn:hover::before, .main-btn:hover::after {
  height: 51%;
  -webkit-transition: all linear 0.5s, height linear 0.5s 0.5s;
  transition: all linear 0.5s, height linear 0.5s 0.5s;
}
.main-btn:hover::before {
  right: 0;
}
.main-btn:hover::after {
  left: 0;
}
.main-btn:hover span {
  color: var(--main-bg);
  -webkit-transition: color ease-out 0.5s 0.7s;
  transition: color ease-out 0.5s 0.7s;
}

.second-main-btn {
